"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "@/components/ui/card";
import { Plus } from "lucide-react";
import Link from "next/link";

type Note = {
  id: string;
  title: string;
  updatedAt: string;
};

export default function DashboardPage() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchNotes() {
      try {
        const res = await api.get("/documents");
        setNotes(res.data);
      } catch (err) {
        console.error("Failed to fetch notes", err);
      } finally {
        setLoading(false);
      }
    }

    fetchNotes();
  }, []);

  if (loading) {
    return <p className="text-muted-foreground">Loading notes...</p>;
  }

  return (
    <div className="container mx-auto px-6 py-8">
      {/* Header */}
      <div className="mb-8 flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold">Your Notes</h1>
          <p className="text-muted-foreground">
            Create and manage your notes
          </p>
        </div>

        <Button className="gap-2">
          <Plus className="h-4 w-4" />
          New Note
        </Button>
      </div>

      {/* Grid */}
      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {/* Add Card */}
        <Card className="flex cursor-pointer items-center justify-center border-dashed hover:border-primary">
          <CardContent className="py-16 text-center text-muted-foreground">
            <Plus className="mx-auto mb-2 h-6 w-6" />
            Add New Note
          </CardContent>
        </Card>

        {/* Notes */}
        {notes.map((note) => (
          <Link key={note.id} href={`/dashboard/documents/${note.id}`}>
            <Card className="cursor-pointer hover:shadow-lg transition">
              <CardHeader>
                <CardTitle>{note.title}</CardTitle>
                <CardDescription>
                  Last updated {new Date(note.updatedAt).toLocaleDateString()}
                </CardDescription>
              </CardHeader>
            </Card>
          </Link>
        ))}
      </div>
    </div>
  );
}
